@tailwind base;
@tailwind components;
@tailwind utilities;

header ul li{
  @apply px-5;
}

.bg-primary{
  background:rgb(2 132 199);
}
.bg-secondary{
  background: #2b2d77;
}

.bg-choice{
  background:rgb(113, 109, 237)
}

.mobile-nav{
  @apply fixed top-12 left-0 bg-gray-800 w-full h-1/4;
}

.mobile-nav  li{
  @apply w-full h-full text-center border-b-2;
}

.project-desc{
  @apply absolute left-0 right-0 bottom-0 top-0 bg-secondary opacity-0 duration-500  hover:opacity-100
}

.btn{
  @apply bg-primary px-5 py-2 font-bold hover:border-2 border-white rounded
}

html{
  scroll-behavior: smooth;
}

